<template>
        <div>
        <div v-for="(error,i) in errors" :key="i">
            <v-alert v-model="error.show" :type="error.type" dismissible>
                {{ error.msg }}
            </v-alert>
        </div>
              <v-card>
  <v-form @submit.prevent="submit" ref="form" v-model="valid" lazy-validation>
            <v-card-title class="grey lighten-4 py-4 title">
              Inicia sessi
            </v-card-title>
            <v-card-text>
            <v-text-field
                autofocus
                v-model="item.email" 
                label="e-Mail" 
                title="e-Mail" 
                required
                :rules="emailRules"
            >
            </v-text-field>
            <v-text-field
                v-model="item.password" 
                :append-icon="show ? 'mdi-eye' : 'mdi-eye_off'"
                :type="show ? 'text' : 'password'"
                @click:append="show = !show"
                hint="Al menys 6 carcters"
                label="Contrasenya" 
                title="Contrasenya" 
                required
            >
            </v-text-field>
            <p><a href="#">He oblidat la meua contrasenya</a></p>
            </v-card-text>
            <v-card-actions>
    <help-button v-if="helpPage" :page="helpPage"></help-button>
    <v-spacer></v-spacer>
    <v-btn type="submit" :disabled="!valid">Login</v-btn>
    <v-btn @click.stop="registerUser">Registrar-se</v-btn>

            </v-card-actions>
  </v-form>
              </v-card>

    </div>    
</template>

<script>
import utilsMixin from '../mixins/utils.js';
import formRulesMixin from '../mixins/formRules.js';
import HelpButton from '../components/base/HelpButton';

export default {
  mixins: [utilsMixin, formRulesMixin],
  components: {HelpButton },
      data() { 
          return {
            helpPage: "login",
            item: {},
            password2: '',
            show: false,
            }
      },
    created() {
      this.$emit('setTitle', 'Inicia sessi');
    },
      methods: {
          submit() {
            this.$store.dispatch('login', this.item)
              .then(resp => {
                this.$store.dispatch('loadData');
              this.$emit('setRol', {
                rol: Number(resp.rol),
                name: resp.name
              });
                if (this.imAlumno) this.$router.push('/ofertas-alum')
                else this.$router.push('/ofertas')
            }) // store the token in localstorage
            .catch(err => {
                this.msgErr(err);
            }); // if the request fails, remove any possible user token if possible
          },
          registerUser() {
            this.$router.push('/register');
          },
      }    
}
</script>
